.footer__wrapper{background-color:var(--background-color-white);margin:0;padding:0;width:100%;color:var(--font-black)}.footer__wrapper .footer__inner{padding:40px 60px;padding:30px 16px;max-width:1280px;margin:0 auto;display:flex;flex-direction:row;align-items:center;display:flex;flex-direction:column;justify-content:space-between;align-items:center}.footer__wrapper img{width:auto;height:auto;margin-bottom:50px}.footer__wrapper .footer__contacts{font-size:16px;line-height:150%;display:flex;flex-direction:row;flex-wrap:nowrap}.footer__wrapper .footer__contacts a{color:var(--font-black)}.footer__wrapper .footer__contacts div:first-child:after{content:"•";padding:0 5px}@media(min-width: 640px){.footer__wrapper .footer__inner{padding:40px 60px;flex-direction:row}.footer__wrapper .footer__inner img{margin:0}}