.separator {
  border: 1px solid var(--global-white);
  width: 100%;
  height: 0px;
  opacity: 0.2;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: var(--font-link);

  &:hover {
    opacity: 0.8;
    transition: all .5s;
  }
}

h1 {
  font-family: 'Unbounded', sans-serif;
  font-weight: 500;
  font-size: 34px;
  line-height: 110%;
  letter-spacing: -0.02em;
  color: var(--font-white);
}

@media (min-width:640px) {
  h1 {
    font-size: 58px;
  }
}

@media (min-width:900px) {
  h1 {
    font-size: 85px;
  }
}

h2 {
  font-family: 'Unbounded', sans-serif;
  font-weight: 600;
  font-size: 24px;
  line-height: 125%;
  color: var(--font-white);
}

@media (min-width:640px) {
  h2 {
    font-size: 32px;
  }
}

h3 {
  font-family: 'Unbounded', sans-serif;
  font-weight: 400;
  font-size: 22px;
  line-height: 120%;
  color: var(--font-white);
}

@media (min-width:640px) {
  h3 {
    font-size: 24px;
  }
}

